@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100&family=Roboto:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400&display=swap");

.header {
  overflow: hidden;
  background-color: #34495e;
}

.header p {
  float: left;
  color: white;
  text-align: center;
  padding: 20px;
  text-decoration: none;
  font-size: 20px;
  list-style: 15px;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  font-family: "Roboto", sans-serif;
}

.header p logo {
  font-size: 25px;
  font-weight: bold;
  color: bisque;
}

.header p:hover {
  background-color: white;
  color: black;
}

.header-right {
  float: right;
}
