input[type="text"],
input[type="number"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #eee;
  border-radius: 4px;
  box-sizing: border-box;
  color: black;
}

input[type="submit"] {
  width: 100%;
  background-color: #f39c12;
  color: #ecf0f1;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type="submit"]:hover {
  background: #27ae60;
}
